import React, { useCallback, useEffect, useState } from 'react';
import Main from 'containers/main';
import readFetch from 'hooks/readFetch';

import bossListConst from 'data/boss';

import DateFnsUtils from '@date-io/date-fns';

import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import { exportData, initialServerTime, parseApiSavedData } from 'utils';
import { noop } from 'lodash';
import { IBoss } from 'types';
import produce from 'immer';
import { IFetch } from 'hooks/useFetch';

import _get from 'lodash/get';

export type IEventCallbackVoid = (event: any) => void;
interface IAppContext {
  serverTime: Date;
  setServerTime: Function;
  updateKillTime: Function;
  onExport: Function;
}

export const AppContext = React.createContext<IAppContext>({
  serverTime: initialServerTime,
  setServerTime: noop,
  updateKillTime: noop,
  onExport: noop,
});

interface IAppArgs {
  fetchBossList: IFetch;
}

const App: React.FC<IAppArgs> = ({ fetchBossList }) => {
  const [serverTime, setServerTime] = useState(initialServerTime);

  const [bossList, setBossList] = useState<IBoss[]>(bossListConst);

  const updateKillTime = useCallback((id, date) => {
    setBossList(
      produce((list: IBoss[]) => {
        const boss = list.find((boss) => boss.id === id);

        if (boss) {
          boss.last_killed = new Date(date);
        }
      })
    );
  }, []);

  const onExport = useCallback(() => {
    exportData(bossList, serverTime);
  }, [bossList, serverTime]);

  const boss = readFetch(fetchBossList);

  console.warn('read', boss);

  /**
    set server time, compare from what recieved from API
    
  */

  useEffect(() => {
    // server time vs local time
    if (Array.isArray(boss) && boss.length) {
      const { config } = boss[0];

      console.warn('config', parseApiSavedData(config));
    }
  }, [boss]);

  return (
    <AppContext.Provider
      value={{
        serverTime,
        setServerTime,
        updateKillTime,
        onExport,
      }}
    >
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <Main bossList={bossList} />
      </MuiPickersUtilsProvider>
    </AppContext.Provider>
  );
};

export default App;
