import { interval, Subject } from 'rxjs';
import { map, throttleTime } from 'rxjs/operators';

const SECONDS_5 = 5 * 1000;

export const updateTime$ = new Subject();

export const updateInterval$ = updateTime$.pipe(
  throttleTime(SECONDS_5),
  map(() => new Date())
);

interval(SECONDS_5).subscribe(() => updateTime$.next());
